import React from 'react';

import { Content, Message, Buttons, CancelButton } from './MessageModal.styles';
import { NewBaseButton } from '../../Button';
import { BaseModal } from '../BaseModal';

export const MessageModal = ({
  isOpen,
  children,
  onConfirm,
  onCancel,
  onClose,
  confirmText,
  cancelText,
}) => {
  return (
    <BaseModal isOpen={isOpen}>
      <Content>
        <Message>{children}</Message>
        <Buttons>
          {onConfirm && (
            <CancelButton
              onClick={() => {
                if (onCancel) {
                  onCancel();
                }
                onClose();
              }}
              theme="primary"
              outline
              size="large"
              block
            >
              {cancelText || ''}
            </CancelButton>
          )}
          <NewBaseButton
            onClick={() => {
              if (onConfirm) {
                onConfirm();
              }
              onClose();
            }}
            theme="primary"
            size={onConfirm === undefined ? 'xlarge' : 'large'}
            block
          >
            {confirmText || ''}
          </NewBaseButton>
        </Buttons>
      </Content>
    </BaseModal>
  );
};
