import React from 'react';
import 'tippy.js/dist/tippy.css';

import { Tooltip } from './InfoTooltip.styles';

export const InfoTooltip = ({
  children,
  content,
  className,
  onClick,
  onShow = () => {},
  visible,
  customAnimation,
}) => {
  /**
   * @description
   * @tippy.js/react   
   * visible prop   trigger  .
   *  children wrapper event    .
   */
  return (
    <Tooltip
      className={className}
      content={<div onClick={onClick}>{content}</div>}
      visible={visible}
      interactive
      arrow
      hideOnClick={false}
      duration={0}
      zIndex={200}
      theme="dcblue"
      placement="bottom-center"
      customAnimation={customAnimation}
      onShow={onShow}
    >
      <div onClick={onClick}>{children}</div>
    </Tooltip>
  );
};
